import React from 'react'
import PropTypes from 'prop-types';
import MainCounter from '../MainCounter/index'
import CounterContainer from '../../containers/CounterContainer'
import Menu from '../Menu/index';


const GlobalInterface = ({increment, decrement, reset, counterContainers, operation, countOfCounters}) => {
    const props = {
        increment: increment,
        decrement: decrement,
        reset: reset
    }
    return (
        <div>
          <Menu tab = 'counters'/>
          <MainCounter {...props} />
          {counterContainers.map((object, i) => <CounterContainer operation={operation} countOfCounters={countOfCounters} key={i}/>)}
        </div>
    )
}

MainCounter.propTypes = {
    increment: PropTypes.func.isRequired,
    decrement: PropTypes.func.isRequired,
    reset: PropTypes.func.isRequired,
    counterContainers: PropTypes.array,
    operation: PropTypes.string
}

export default GlobalInterface


